import { notFound } from 'next/navigation';
import ApiSidebar from '@/app/components/ApiSidebar';
import { API_DATA } from '@/app/api/apiData';
import { Endpoint } from '@/app/api/apiData';

interface EndpointPageProps {
  params: {
    endpointId: string;
  };
}

export function generateStaticParams() {
  const endpoints = API_DATA.flatMap(category => 
    category.endpoints.map(endpoint => ({ endpointId: endpoint.id }))
  );
  
  return endpoints;
}

function getEndpoint(endpointId: string): Endpoint | undefined {
  for (const category of API_DATA) {
    const endpoint = category.endpoints.find(e => e.id === endpointId);
    if (endpoint) return endpoint;
  }
  return undefined;
}

export default async function EndpointPage({ params }: EndpointPageProps) {
  const endpointId = params?.endpointId;
  if (!endpointId) {
    notFound();
  }
  
  const endpoint = getEndpoint(endpointId);
  
  if (!endpoint) {
    notFound();
  }

  return (
    <div className="flex min-h-screen bg-gray-50">
      <ApiSidebar activeEndpoint={endpointId} />
      <div className="flex-1 p-4 md:p-8">
        <div className="max-w-4xl mx-auto bg-white shadow-sm rounded-lg p-6 md:p-8">
          <div className="border-b border-gray-200 mb-8 pb-4">
            <h1 className="text-2xl font-bold mb-2 text-gray-900">{endpoint.name}</h1>
            <div className="flex items-center">
              <span className={`text-sm font-mono px-2 py-1 mr-2 rounded-md
                ${endpoint.method === 'GET' ? 'method-get' : ''}
                ${endpoint.method === 'POST' ? 'method-post' : ''}
                ${endpoint.method === 'PUT' ? 'method-put' : ''}
                ${endpoint.method === 'DELETE' ? 'method-delete' : ''}
              `}>
                {endpoint.method}
              </span>
              <span className="font-mono text-gray-600">{endpoint.url}</span>
            </div>
            <p className="mt-4 text-gray-600">{endpoint.description}</p>
          </div>

          <div className="mb-10">
            <h2 className="text-xl font-semibold mb-4 text-primary">Request</h2>
            
            <div className="space-y-6">
              {endpoint.headers && Object.keys(endpoint.headers).length > 0 && (
                <div>
                  <h3 className="text-lg font-medium mb-3 text-gray-900">Headers</h3>
                  <div className="bg-gray-50 p-4 rounded-md border border-gray-200">
                    <div className="overflow-x-auto">
                      <table className="min-w-full divide-y divide-gray-200">
                        <thead>
                          <tr>
                            <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Header</th>
                            <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Value</th>
                          </tr>
                        </thead>
                        <tbody className="divide-y divide-gray-200">
                          {Object.entries(endpoint.headers || {}).map(([key, value]) => (
                            <tr key={key}>
                              <td className="px-4 py-2 text-sm font-mono">{key}</td>
                              <td className="px-4 py-2 text-sm font-mono">{value}</td>
                            </tr>
                          ))}
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
              )}

              {endpoint.parameters && Object.entries(endpoint.parameters).length > 0 && (
                <div>
                  <h3 className="text-lg font-medium mb-3 text-gray-900">Parameters</h3>
                  <div className="bg-gray-50 p-4 rounded-md border border-gray-200">
                    <div className="overflow-x-auto">
                      <table className="min-w-full divide-y divide-gray-200">
                        <thead>
                          <tr>
                            <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Parameter</th>
                            <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Type</th>
                            <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Required</th>
                            <th className="px-4 py-2 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Description</th>
                          </tr>
                        </thead>
                        <tbody className="divide-y divide-gray-200">
                          {Object.entries(endpoint.parameters).map(([name, param]) => (
                            <tr key={name}>
                              <td className="px-4 py-2 text-sm font-mono">{name}</td>
                              <td className="px-4 py-2 text-sm">{param.type}</td>
                              <td className="px-4 py-2 text-sm">{param.required ? 'Yes' : 'No'}</td>
                              <td className="px-4 py-2 text-sm">{param.description}</td>
                            </tr>
                          ))}
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
              )}

              {endpoint.requestBody && (
                <div>
                  <h3 className="text-lg font-medium mb-3 text-gray-900">Request Body</h3>
                  <div className="bg-gray-50 p-4 rounded-md border border-gray-200">
                    <pre className="text-sm overflow-x-auto font-mono">
                      {JSON.stringify(endpoint.requestBody, null, 2)}
                    </pre>
                  </div>
                </div>
              )}
            </div>
          </div>

          <div className="mb-10">
            <h2 className="text-xl font-semibold mb-4 text-primary">Responses</h2>
            
            <div className="space-y-6">
              {endpoint.responses && Object.entries(endpoint.responses).map(([status, response]) => (
                <div key={status}>
                  <div className="flex items-center mb-3">
                    <span className={`text-sm px-2 py-1 rounded-md mr-2 ${
                      parseInt(status) >= 200 && parseInt(status) < 300
                        ? 'bg-green-100 text-green-800'
                        : parseInt(status) >= 400
                        ? 'bg-red-100 text-red-800'
                        : 'bg-yellow-100 text-yellow-800'
                    }`}>
                      {status}
                    </span>
                    <span className="text-gray-700">{response.description}</span>
                  </div>
                  
                  {response.example && (
                    <div className="bg-gray-50 rounded-lg overflow-hidden border border-gray-200">
                      <div className="bg-gray-100 px-4 py-2 border-b border-gray-200 font-mono text-sm">
                        Response Body
                      </div>
                      <div className="p-4">
                        <pre className="text-sm overflow-x-auto font-mono">
                          {JSON.stringify(response.example, null, 2)}
                        </pre>
                      </div>
                    </div>
                  )}
                </div>
              ))}
            </div>
          </div>

          {endpoint.sampleCode && (
            <div className="mb-4">
              <h2 className="text-xl font-semibold mb-4 text-primary">Code Examples</h2>
              
              <div className="space-y-6">
                {endpoint.sampleCode.curl && (
                  <div className="bg-gray-50 rounded-lg overflow-hidden border border-gray-200">
                    <div className="bg-gray-100 px-4 py-2 border-b border-gray-200 font-mono text-sm">
                      cURL
                    </div>
                    <div className="p-4">
                      <pre className="text-sm overflow-x-auto font-mono">
                        {endpoint.sampleCode.curl}
                      </pre>
                    </div>
                  </div>
                )}
                
                {endpoint.sampleCode.javascript && (
                  <div className="bg-gray-50 rounded-lg overflow-hidden border border-gray-200">
                    <div className="bg-gray-100 px-4 py-2 border-b border-gray-200 font-mono text-sm">
                      JavaScript
                    </div>
                    <div className="p-4">
                      <pre className="text-sm overflow-x-auto font-mono">
                        {endpoint.sampleCode.javascript}
                      </pre>
                    </div>
                  </div>
                )}
                
                {endpoint.sampleCode.python && (
                  <div className="bg-gray-50 rounded-lg overflow-hidden border border-gray-200">
                    <div className="bg-gray-100 px-4 py-2 border-b border-gray-200 font-mono text-sm">
                      Python
                    </div>
                    <div className="p-4">
                      <pre className="text-sm overflow-x-auto font-mono">
                        {endpoint.sampleCode.python}
                      </pre>
                    </div>
                  </div>
                )}
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
} 